@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
*{margin:0}* .App{text-align:center}* .App .App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}* .App .App-header #quote-box{background-color:white;border-radius:10px;font-family:"Poppins", sans-serif;color:black;padding:25px;margin:0 25px;max-width:500px}* .App .App-header #quote-box #text{font-size:28px;text-align:center;padding:0 20px 10px}* .App .App-header #quote-box #text span{font-family:sans-serif;font-size:100px;padding-right:10px;position:relative;top:50px}* .App .App-header #quote-box #author{text-align:end;padding-bottom:20px}* .App .App-header #quote-box .footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:10px 0}* .App .App-header #quote-box .footer .btn{border:none;border-radius:4px;cursor:pointer}* .App .App-header #quote-box .footer #tweet-quote{background-color:#282c34;color:white;text-decoration:none;padding:10px 15px}* .App .App-header #quote-box .footer #new-quote{background-color:#282c34;color:white;padding:10px 15px}

